import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import Box from '@mui/material/Box';

const dispatchedEvents = [];

const SrsMuiBox = function (props, ref) {
  return (
    <Box {...props} theme={props.muiTheme}>
      <slot />
    </Box>
  );
}

const styles = `
  /* Custom styles for the Box component */
`;

const props = [
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(React.forwardRef(SrsMuiBox), props, "mui");
export { WrappedComponent, styles, props, dispatchedEvents, slots };
